<template>
  <div class="page">
    <div class="contianer">
      <div class="shell-panel">
        <a-menu v-model:selectedKeys="currentLisenterName" mode="horizontal">
          <template v-for="listener in listenerList" :key="listener.name">
            <a-menu-item>
              <template #icon>
                <mail-outlined />
              </template>
              {{ listener.name }}
            </a-menu-item>
          </template>
        </a-menu>
        <div class="payload-content">
          <a-layout>
            <a-layout-sider width="200" style="background: #fff">
              <a-menu mode="inline" :style="{ height: '100%', borderRight: 0 }">
                <a-menu-item v-for="beacon in beaconList" :key="beacon.uuid" @click="beaconBtnHandler(beacon.name)">
                  <template #icon>
                    <mail-outlined />
                  </template>
                  {{beacon.name}}
                </a-menu-item>
              </a-menu>
            </a-layout-sider>
            <a-layout-content
              :style="{
                background: '#fff',
                padding: '24px',
                margin: 0,
                minHeight: '280px',
              }"
            >
              <terminal :targetUrl="currentBeaconTargetUrl" />
            </a-layout-content>
          </a-layout>
        </div>
      </div>
      <div class="listener-panel">
        <a-card title="Listener">
          <template class="ant-card-actions" #actions>
            <setting-outlined key="setting"/>
            <add-listener-modal key="add"  @refresh-data-list="genListenerList" />
            <ellipsis-outlined key="ellipsis" />
          </template>
          <listener-table :listenerList="listenerList" />
        </a-card>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import {
  SettingOutlined,
  EllipsisOutlined,
  MailOutlined,
} from "@ant-design/icons-vue";
import { defineComponent, onMounted, ref, watch } from "vue";
import { GetListenerListAPI } from "@/api/listener";
import { GetBeaconListAPI} from '@/api/beacon';
import Terminal from "@/components/listener/Terminal.vue";
import AddListenerModal from "./AddListenerModal.vue";
import { Beacon } from '@/typings/beacon';
import {useStore} from '@/store';
import { WSDataType } from "@/typings/billboard";
import ListenerTable from './ListenerTable.vue';

interface TableListenerI {
  name: string;
  address: string;
  tags: string[];
}

export default defineComponent({
  components: {
    Terminal,
    SettingOutlined,
    EllipsisOutlined,
    MailOutlined,
    AddListenerModal,
    ListenerTable,
  },
  setup() {
    const store = useStore();

    const currentLisenterName = ref<string[]>(["a"]);
    const listenerList = ref<TableListenerI[]>([]);
    const beaconList = ref<Beacon[]>([]);
    const currentBeaconTargetUrl = ref<string>("");

    const genListenerList = async () => {
      const res = await GetListenerListAPI();
      console.log(res);
      listenerList.value = []
      if (res) {
        const data = res.data.data;
        if(data){
          data.forEach((listener) => {
            listenerList.value.push({
              name: listener.name,
              address: `${listener.lhost}:${listener.lport}`,
              tags: [listener.closed ? "Offline" : "Online"],
            });
          });
        }
      }
    };

    watch(
      () => store.state.billboard.incommingWSData,
      (newVal, oldVal) => {
        console.log(`received ${newVal}`);
        if (newVal?.type === WSDataType.BEACON) {
          genBeaconList()
        }
      }
    );

    const genBeaconList = async () => {
      const res = await GetBeaconListAPI();
      console.log(res);
      beaconList.value = []
      if (res) {
        const data = res.data.data;
        if(data){
          data.forEach((beacon) => {
            beaconList.value.push({
              name: beacon.name,
              uuid: beacon.uuid,
            });
          });
        }
      }
    };


    const beaconBtnHandler = (beaconName: string) => {
      console.log("beaconBtnHandler ", beaconName);
      const url = new URL('/api/v1/beacon/ws/', window.location.href);
      url.protocol = url.protocol.replace('http', 'ws');
      currentBeaconTargetUrl.value = `${url.href}?name=${beaconName}`;
    };

    onMounted(() => {
      genListenerList();
      genBeaconList();
    });

    return {
      listenerList,
      beaconList,
      currentLisenterName,
      currentBeaconTargetUrl,
      beaconBtnHandler,
      genListenerList,
    };
  },
});
</script>


<style lang="scss" scoped>
.contianer {
  margin: 24px 16px;
  padding: 24px;
  min-height: 280px;
}
.listener-panel {
  margin-top: 24px;
}
</style>