<template>
  <a-table :columns="columns" :data-source="listenerList">
    <template #name="{ text }">
      <a>{{ text }}</a>
    </template>
    <template #customTitle>
      <span>
        Name
      </span>
    </template>
    <template #tags="{ text: tags }">
      <span>
        <a-tag
          v-for="tag in tags"
          :key="tag"
          :color="
            tag === 'Offline'
              ? 'volcano'
              : tag.length > 5
              ? 'geekblue'
              : 'green'
          "
        >
          {{ tag.toUpperCase() }}
        </a-tag>
      </span>
    </template>
    <template #action="{ record }">
      <span>
        <a>Gen Payload for {{ record.name }}</a>
        <a-divider type="vertical" />
        <a>Delete</a>
        <a-divider type="vertical" />
        <a class="ant-dropdown-link">
          More actions
          <down-outlined />
        </a>
      </span>
    </template>
  </a-table>
</template>

<script lang="ts">
import { defineComponent,PropType } from "vue";
import {Listener} from '@/typings/listener';
const columns = [
  {
    dataIndex: "name",
    key: "name",
    slots: { title: "customTitle", customRender: "name" },
  },
  {
    title: "Address",
    dataIndex: "address",
    key: "address",
  },
  {
    title: "Status",
    key: "tags",
    dataIndex: "tags",
    slots: { customRender: "tags" },
  },
  {
    title: "Action",
    key: "action",
    slots: { customRender: "action" },
  },
];


export default defineComponent({
  props: {
    listenerList: {
      type: Array as PropType<Listener[]>,
      required: true,
    },
  },
  setup(prop) {
    const genPayload = (address:string):string=>{
      const host = address.split(":")[0]
      const port = address.split(":")[1]
      const url = new URL(`/gen/${host}/${port}`, window.location.href);
      return url.href;
    }

    return {
      columns
    };
  },
});
</script>

