
import { defineComponent,PropType } from "vue";
import {Listener} from '@/typings/listener';
const columns = [
  {
    dataIndex: "name",
    key: "name",
    slots: { title: "customTitle", customRender: "name" },
  },
  {
    title: "Address",
    dataIndex: "address",
    key: "address",
  },
  {
    title: "Status",
    key: "tags",
    dataIndex: "tags",
    slots: { customRender: "tags" },
  },
  {
    title: "Action",
    key: "action",
    slots: { customRender: "action" },
  },
];


export default defineComponent({
  props: {
    listenerList: {
      type: Array as PropType<Listener[]>,
      required: true,
    },
  },
  setup(prop) {
    const genPayload = (address:string):string=>{
      const host = address.split(":")[0]
      const port = address.split(":")[1]
      const url = new URL(`/gen/${host}/${port}`, window.location.href);
      return url.href;
    }

    return {
      columns
    };
  },
});
