import service from "@/utils/request";
import { IResponse } from "@/typings/response";
import { AxiosPromise } from "axios";
import { Listener } from "@/typings/listener";

// @Summary 
// @Produce  application/json
// @Router /listener/ [get]
export function GetListenerListAPI(): AxiosPromise<IResponse<Listener[]|null>> {
  return service({
    url: 'listener/',
    method: "get",
  });
}


// @Summary 
// @Produce  application/json
// @Router /listener/ [post]
export function AddListenerAPI(listener:Listener): AxiosPromise<IResponse<Listener>> {
    return service({
      url: 'listener/',
      method: "post",
      data:listener
    });
  }
  