
import { defineComponent, onMounted, onUnmounted, Ref, ref, watch } from "vue";
import "xterm/css/xterm.css";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
import { ShData,ShDataType } from "@/typings/beacon";
import { createToast } from "mosha-vue-toastify";
// import the styling for the toast
import "mosha-vue-toastify/dist/style.css";
import {useStore} from '@/store';

const defaultTheme = {
  foreground: "#ffffff", // 
  background: "#1b212f", // 
  cursor: "#ffffff", // 
  selection: "rgba(255, 255, 255, 0.3)",
  black: "#000000",
  brightBlack: "#808080",
  red: "#ce2f2b",
  brightRed: "#f44a47",
  green: "#00b976",
  brightGreen: "#05d289",
  yellow: "#e0d500",
  brightYellow: "#f4f628",
  magenta: "#bd37bc",
  brightMagenta: "#d86cd8",
  blue: "#1d6fca",
  brightBlue: "#358bed",
  cyan: "#00a8cf",
  brightCyan: "#19b8dd",
  white: "#e5e5e5",
  brightWhite: "#ffffff",
};

const enum SpecialTerminalChar{
  BACKSPACE = "\x7f",
  ENETER = "\x0d",
  UP = "\x1b[A",
  LEFT = "\x1b[D",
  DOWN = "\x1b[B",
  RIGHT = "\x1b[C",
  CRTL_C = "\x03",
  CRTL_v = "\x16",
}

export default defineComponent({
  props: {
    targetUrl: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const store = useStore();

    const term: Ref<HTMLElement | null> = ref(null);
    const terminal: Ref<Terminal | null> = ref(null);
    const socket: Ref<WebSocket | null> = ref(null);
    const fitAddon: Ref<FitAddon> = ref(new FitAddon());
    


    var inputBuffer = "";
    var currentPosOfInputBuffer = 0;
    var lastInputChar ="";

    const startTerminal = function (): void {
      if (term.value) {
        terminal.value = new Terminal({
          rendererType: "canvas", // 
          convertEol: true, // 
          scrollback: 50, // 
          disableStdin: true, // 
          cursorStyle: "bar", // 
          cursorBlink: true, // 
          theme: defaultTheme,
        });
        terminal.value.loadAddon(fitAddon.value);
        terminal.value.open(term.value);
        ///fitAddon.value.fit();

        window.addEventListener("resize", () => {
          try {
            console.log("fit");
            fitAddon.value.fit();
          } catch (e) {
            console.log(e);
          }
        });

        terminal.value.onData((inputChar: string) => {
          // 
          //console.log("before",escape(inputChar),inputBuffer,currentPosOfInputBuffer);
          var msg:ShData;
          var leftp:string =inputBuffer.slice(0,currentPosOfInputBuffer);
          var rightp:string=inputBuffer.slice(currentPosOfInputBuffer,inputBuffer.length);

          //console.log("left part",leftp,"righ part",rightp)
          switch(inputChar){
            case SpecialTerminalChar.BACKSPACE:
              if(currentPosOfInputBuffer <=0)
                break;
              if(currentPosOfInputBuffer === 0) 
                break;
              terminal.value?.write("\b\u001b[1P"+rightp+"\b".repeat(rightp.length));
              inputBuffer = inputBuffer.slice(0,currentPosOfInputBuffer-1) + rightp;
              currentPosOfInputBuffer -=1;
              break;
            case SpecialTerminalChar.ENETER:
              msg = { type: ShDataType.CMD, data: inputBuffer+"\n" };
              terminal.value?.write("\b".repeat(leftp.length));
              inputBuffer = "";
              currentPosOfInputBuffer = 0;
              if (socket.value) socket.value.send(JSON.stringify(msg));
              break;
            case SpecialTerminalChar.UP:
              terminal.value?.write("\b".repeat(inputBuffer.length));
              msg = { type: ShDataType.CMD, data: inputChar };
              if (socket.value) socket.value.send(JSON.stringify(msg));
              break;
            case SpecialTerminalChar.DOWN:
              terminal.value?.write("\b".repeat(inputBuffer.length));
              msg = { type: ShDataType.CMD, data: inputChar };
              if (socket.value) socket.value.send(JSON.stringify(msg));
              break;
            case SpecialTerminalChar.LEFT:
              if(currentPosOfInputBuffer <=0) {
                currentPosOfInputBuffer = 0
              }else{
                currentPosOfInputBuffer -= 1;
                terminal.value?.write(inputChar);
              }
              break;
            case SpecialTerminalChar.RIGHT:
              if(currentPosOfInputBuffer >= inputBuffer.length) {
                currentPosOfInputBuffer = inputBuffer.length
              }else{
                currentPosOfInputBuffer += 1;
                terminal.value?.write(inputChar);
              }
              break;
            case SpecialTerminalChar.CRTL_C:
              terminal.value?.write(inputChar);
              break;
            default:
              terminal.value?.write(inputChar+rightp+"\b".repeat(rightp.length));
              inputBuffer = leftp + inputChar + rightp
              currentPosOfInputBuffer += 1;
          }
          lastInputChar = inputChar;

          //console.log("after",escape(inputChar),inputBuffer,currentPosOfInputBuffer);
          //leftp = inputBuffer.slice(0,currentPosOfInputBuffer);
          //rightp =inputBuffer.slice(currentPosOfInputBuffer,inputBuffer.length);

          console.log("left part",leftp,"righ part",rightp)
          //
          //const msg: ShData = { type: ShDataType.CMD, data: inputChar };
          //if (socket.value) socket.value.send(JSON.stringify(msg));
          //console.log("after",escape(inputChar),inputBuffer,currentPosOfInputBuffer);
        });
      }
    };

    const startWsConn = (url: string) => {
      if (url.length <= 0) {
        return;
      }
      if (socket.value) {
        socket.value.close();
      }
      socket.value = new WebSocket(url);
      socket.value.onopen = (e: Event) => {
        console.log("Connected to server");
        sendSuccessToast("Connected to server");
        if (socket.value){
          socket.value.send(JSON.stringify({ type: "auth", data: store.state.user.accessToken }));
          socket.value.send(JSON.stringify({ type: "cmd", data: "\n" }));
          terminal.value?.setOption("disableStdin",false)
        }
      };
      socket.value.onmessage = (e: MessageEvent) => {
        const msg: ShData = JSON.parse(e.data);
        switch (msg.type){
          case ShDataType.DEBUG:
            console.log("shData debug",msg.data)
            break
          case ShDataType.CMD:
            switch (lastInputChar){
              case SpecialTerminalChar.UP:
                inputBuffer = msg.data
                currentPosOfInputBuffer = inputBuffer.length
                break
              case SpecialTerminalChar.DOWN:
                inputBuffer = msg.data
                currentPosOfInputBuffer = inputBuffer.length
                break
              case SpecialTerminalChar.ENETER:
                inputBuffer = ""
                currentPosOfInputBuffer = 0
                break
            }
            terminal.value?.write(msg.data)
            break
          case ShDataType.ERROR:
            sendErrToast(msg.data)
            break
        }
      };
      socket.value.onclose = (e: Event) => {
        console.log("Connection Closed");
        terminal.value?.setOption("cursorBlink", false);
        sendErrToast("Connection Closed");
      };
      socket.value.onerror = (e: Event) => {
        console.log("Failed");
      };
    };

    const sendErrToast = (errMsg: string) => {
      createToast(errMsg, {
        showIcon: true,
        position: "bottom-right",
        hideProgressBar: true,
        type: "danger",
      });
    };

    const sendSuccessToast = (successMsg: string) => {
      createToast(successMsg, {
        showIcon: true,
        position: "bottom-right",
        hideProgressBar: true,
        type: "success",
      });
    };

    watch(
      () => props.targetUrl,
      (newVal, oldVal) => {
        console.log(`listener changed: old ${oldVal} new ${newVal}`);
        startWsConn(newVal);
      }
    );

    onMounted(() => {
      startWsConn(props.targetUrl);
      startTerminal();
    });

    onUnmounted(() => {
      if (socket.value) {
        socket.value.close();
      }
    });

    return {
      term,
      terminal,
      startTerminal,
    };
  },
  methods: {},
});
