<template>
  <div>
    <edit-outlined @click="showModal" />
    <a-modal
      title="Title"
      v-model:visible="visible"
      :confirm-loading="confirmLoading"
      @ok="handleSumbit"
    >
      <a-form
        :model="formState"
      >
        <a-form-item label="Listener name">
          <a-input v-model:value="formState.name" />
        </a-form-item>
        <a-form-item label="Listener host">
          <a-input v-model:value="formState.lhost" />
        </a-form-item>
        <a-form-item label="Listener port">
          <input-number
            v-model:value="formState.lport"
            :default-value="0"
            :min="0"
            :max="65535"
          />
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>


<script lang="ts">
import { EditOutlined } from "@ant-design/icons-vue";
import {AddListenerAPI} from '@/api/listener';
import {InputNumber} from 'ant-design-vue'
import { ref, defineComponent,reactive, toRaw, UnwrapRef  } from "vue";
import {Listener} from '@/typings/listener'

export default defineComponent({
  components: {
    EditOutlined,
    InputNumber,
  },
  emits:['refresh-data-list'],
  setup(context) {
    const modalText = ref<string>("Content of the modal");
    const visible = ref<boolean>(false);
    const confirmLoading = ref<boolean>(false);

    const formState: UnwrapRef<Listener> = reactive({
      name: '',
      lhost:'0.0.0.0',
      lport:0,
    });

    const showModal = () => {
      visible.value = true;
    };

    return {
      modalText,
      visible,
      confirmLoading,
      formState,
      showModal,
    };
  },
  methods:{
    handleSumbit:async function (){
      this.confirmLoading = true;
      const res = await AddListenerAPI(this.formState)
      console.log(res)
      if (res){
        this.visible = false;
        this.$emit('refresh-data-list');
      }
      this.confirmLoading = false;
    }
  }
});
</script>